<h2>API</h2>
<p>更多详细信息请参考 <u-link href="https://echarts.apache.org/zh/api.html">Echarts 的 API 文档</u-link>。</p>
<h3>Props/Attrs（均为响应式）</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>initOptions</td>
<td>Object</td>
<td></td>
<td>初始化实例</td>
</tr>
<tr>
<td>options</td>
<td>Object</td>
<td></td>
<td>ECharts 实例的数据。修改这个 prop 会触发 ECharts 实例的 <code>setOption</code> 方法。如果直接修改 <code>options</code> 绑定的数据而对象引用保持不变，<code>setOption</code> 方法调用时将带有参数 <code>notMerge: false</code>。否则，如果为 <code>options</code> 绑定一个新的对象，<code>setOption</code> 方法调用时则将带有参数 <code>notMerge: true</code>。</td>
</tr>
<tr>
<td>theme</td>
<td>String</td>
<td></td>
<td>设置主题</td>
</tr>
<tr>
<td>group</td>
<td>String</td>
<td></td>
<td>实例的分组，会自动绑定到 ECharts 组件的同名属性上。</td>
</tr>
<tr>
<td>autoresize</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>指定 ECharts 实例在组件根元素尺寸变化时是否需要自动进行重绘。</td>
</tr>
<tr>
<td>manual-update</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>在性能敏感（数据量很大）的场景下，我们最好对于 <code>options</code> prop 绕过 Vue 的响应式系统。当将 <code>manual-update</code> prop 指定为 <code>true</code> 且不传入 <code>options</code> prop 时，数据将不会被监听。然后，你需要用 <code>ref</code> 获取组件实例以后手动调用 <code>mergeOptions</code> 方法来更新图表。</td>
</tr>
</tbody>
</table>
<h3>Computed</h3>
<table>
<thead>
<tr>
<th>Computed</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td>用来获取 ECharts 实例的当前宽度。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td></td>
<td>用来获取 ECharts 实例的当前高度。</td>
</tr>
<tr>
<td>computedOptions</td>
<td>Object</td>
<td></td>
<td>用来读取 ECharts 更新内部 <code>options</code> 后的实际数据。</td>
</tr>
</tbody>
</table>
